s all events from `Hobbit` and `Lord of the Rings` took place during this period. ")]),_c('br'),_c('h5',[_vm._v(" In the following visualization you can explore many questions. Where is Lonely Mountain with the treasure of Smaug? How far away from home did Frodo and Sam went? Why Minas Tirith strategic position was so importand both for Mordor and Allies? Just use scroll wheel of your mouse to zoom, use search bar to find specific place of interest or display path of every hero using checkboxes. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"text-container",attrs:{"id":"TimelineAnchor"}},[_c('h2',[_vm._v("Timeline")]),_c('h5',[_vm._v(" The history of Tolkien’s universe is complicated, to say the least. We’ve put together this timeline to take you through the major storyline of Lord of the Rings. ")]),_c('br'),_c('h5',[_vm._v(" In the following visualization you can explore the story of our heroes and interaction between eachother. When did Frodo and Gollum meet for the first time? When did Gandalf become white ? and many other questions... Simply scroll to zoom and drag the graph to move in timeline, and move your mouse over the dots to see event descriptions. Also you can select your favorite character from the right checklist. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"text-container",attrs:{"id":"KingsAnchor"}},[_c('h2',[_vm._v("Kings of Gondor")]),_c('h5',[_vm._v(" Gondor is the largest kingdom of men in the west of Middle-earth in Tolkien’s epic Lord of the Rings series, and the center of the military strength that will fight Sauron at the end of the Third Age. Gondor was in a very crucial position, in order to guard Middle Earth and because of Gondor's proximity to Mordor, its military forces had to be proficiently trained and vigilantly on guard at all times. Because of this, they were arguably the strongest country that opposed Sauron. ")]),_c('br'),_c('h5',[_vm._v(" In the following visualization you can explore the rulers of this might Kingdom. Which king has the longest ruling time? When was Gondor re-united after it's civil war. You can drag the timeline to explore, click on the names to see the details, and zoom in/out by using the left zoom icons. ")])])}]

